//Random color generator
//var r = () => Math.random() * 256 >> 0;
//var color = `rgb(${r()}, ${r()}, ${r()})`;
//import number from '../App'
const initalState = {
  loading: false,
  data: {
    labels: [],
    datasets: [{
      type: 'line',
      label: "BTC close",
      data: [],
      backgroundColor: 'rgba(226, 153, 18, 0.9)',
      borderColor: 'rgba(178, 116, 0, 1)',
      pointBorderColor: 'rgba(25, 16, 0, 1)',

    }],
    
  },
  dataB: {
    labels: [],
    datasets: [{
      type: 'radar',
      label: "BOT CHART PREDICTION",
      data: [],
      backgroundColor: 'rgba(226, 153, 18, 0.9)',
      borderColor: 'rgba(178, 116, 0, 1)',
      pointBorderColor: 'rgba(25, 16, 0, 1)',
      options: {
        responsive: true
      }
    }]
  },
  dataC: {
    labels: [],
    datasets: [{
      type: 'bar',
      label: "BOT CHART PREDICTION",
      data: [],
      backgroundColor: 'rgba(226, 153, 18, 0.9)',
      borderColor: 'rgba(178, 116, 0, 1)',
      pointBorderColor: 'rgba(25, 16, 0, 1)',
      options: {
        responsive: true
      }
    }]
  },
};

const bitcoinReducer = (state = initalState, action) => {
  const { type, payload } = action;

  switch (type) {
    case "AWAITING_BITCOIN":
      return {
        ...state,
        loading: true
      }
    case "REJECTED_BITCOIN":
      return {
        ...state,
        loading: false,
      }
    case "SUCCESS_BITCOIN":
      return {
        ...state,
        loading: false,
        data: {
          labels: payload.labels,
          datasets: [{
            label: "BTC CLOSE",
            data: payload.close,
            backgroundColor: 'rgba(226, 153, 18, 0.9)',
            borderColor: 'rgba(178, 116, 0, 1)',
            pointBorderColor: 'rgba(25, 16, 0, 1)',
            order: 3
          },
          {
            type: 'line',
            label: "BTC OPEN",
            data: payload.open,
            backgroundColor: 'rgba(10, 204, 0, 0.3)',
            borderColor: 'rgba(10, 204, 0, 1)',
            pointBorderColor: 'rgba(25, 16, 0, 1)',
            order: 2
          },{
            type: 'line',
            label: "BTC HIGH",
            data: payload.high,
            backgroundColor:'rgba(22, 91, 160, 0.9)',
            borderColor: 'rgba(14, 38, 62,0.9)',
            pointBorderColor: 'rgba(22, 91, 160, 1)',
            order: 4               
          },{
            type: 'line',
            label: "BTC LOW",
            data: payload.low,
            backgroundColor:'rgba(233, 190, 0, 0.9)',
            borderColor: 'rgba(255,255,0, 0.9)',
            pointBorderColor: 'rgba(255,255,0, 0.9)',
            order: 1
          }],

       },
        dataB: {
          labels: ['OpenBrainResult', 'CloseBrainResult', 'HighBrainResult', 'LowBrainResult'],
          datasets: [{
            type: 'radar',
            label: "OPEN PRICE BRAIN PREDICTION",
            data: [payload.brOP[0], payload.brOP[0], payload.brOP[0], payload.brOP[0]],// + (number + 1)],
            backgroundColor: 'rgba(255, 0, 0, 0.4)',
            borderColor: '	rgba(255, 0, 0, 0.9)',
            pointBorderColor: 'rgba(25, 16, 0, 1)',

            order: 1
            },
            {
            type: 'radar',
            label: "CLOSE PRICE BRAIN PREDICTION",
            data: [payload.CloseBrainResult, payload.CloseBrainResult, payload.CloseBrainResult, payload.CloseBrainResult],
            backgroundColor:'rgba(10, 204, 0, 0.7)',
            borderColor: 'rgba(10, 204, 0, 0.9)',
            pointBorderColor: 'rgba(10, 204, 0, 0.7)',

            order: 2
          },
          {
            type: 'radar',
            label: "HIGH PRICE BRAIN PREDICTION",
            data: [payload.HighBrainResult, payload.HighBrainResult, payload.HighBrainResult, payload.HighBrainResult],
            backgroundColor:'rgba(0,0,255, 0.7)',
            borderColor: 'rgba(0,0,255, 0.9)',
            pointBorderColor: 'rgba(0,0,255, 0.8)',

            order: 3
          },
          {
            type: 'radar',
            label: "LOW PRICE BRAIN PREDICTION",
            data: [payload.LowBrainResult, payload.LowBrainResult, payload.LowBrainResult, payload.LowBrainResult],
            backgroundColor:'rgba(255,255,0, 0.8)',
            borderColor: 'rgba(255,255,0, 0.9)',
            pointBorderColor: 'rgba(255,255,0, 0.9)',

            order: 4
          }]
          },
           dataC: {
            labels: ['Close Prediction', 'Open Prediction'],
            datasets: [{
              type: 'bar',
              label: "Close - Open",
              data: [payload.CloseBrainResult - payload.brOP[0]],
              backgroundColor:'rgba(10, 204, 0, 0.7)',
              borderColor: 'rgba(10, 204, 0, 0.9)',
              pointBorderColor: 'rgba(10, 204, 0, 0.7)',
              order: 5
            },
            {
              type: 'bar',
              label: "Open - Close",
              data: [payload.brOP[0] - payload.CloseBrainResult],
              backgroundColor: 'rgba(255, 0, 0, 0.4)',
              borderColor: '	rgba(255, 0, 0, 0.9)',
              pointBorderColor: 'rgba(25, 16, 0, 1)',
              order: 6
            },
            {
              type: 'bar',
              label: "High - Low",
              data: [payload.HighBrainResult - payload.LowBrainResult],
              backgroundColor: 'rgba(46, 80, 255, 1)',
              borderColor: 'rgba(46, 80, 255, 0.6)',
              pointBorderColor: 'rgba(25, 16, 0, 1)',
              order: 7
            },
            {
              type: 'bar',
              label: "Low - High",
              data: [payload.LowBrainResult - payload.HighBrainResult],
              backgroundColor: 'rgba(255, 229, 46, 0.9)',
              borderColor: 'rgba(255, 229, 46, 0.6)',
              pointBorderColor: 'rgba(255, 229, 46, 0.9)',
              order: 8
            },
            {
              type: 'bar',
              label: "Real Time - Open Prediction",
              data: [payload.opVS - payload.brOP],
              backgroundColor: 'rgb(0, 0, 0, 0.6)',
              borderColor: 'rgb(0, 0, 0)',
              pointBorderColor: 'rgb(0, 0, 0)',
              order: 9
            },            {
              type: 'bar',
              label: "Open Prediction - Real Time",
              data: [payload.brOP - payload.opVS],
              backgroundColor: 'rgb(0, 0, 0, 0.6)',
              borderColor: 'rgb(0, 0, 0)',
              pointBorderColor: 'rgb(0, 0, 0)',
              order: 10
            }
           ],
           },
             options:{
               responsive : true
                }
               }
                default: return state;
                }
               }
                export default bitcoinReducer;
      
