

const initalState = {
    Loading: false,
    data: {
        labels: [],
        datasets: [{
          type: 'line',
          label: "Real Time vs Open Prediction",
          data: [],
          backgroundColor: 'rgba(226, 153, 18, 0.9)',
          borderColor: 'rgba(178, 116, 0, 1)',
          pointBorderColor: 'rgba(25, 16, 0, 1)',
          options: {
            scales: {
                x: {
                    ticks: {
                        // Include a dollar sign in the ticks
                        callback: function(value, index, values) {
                            return '$' + value;
                        }
                    }
                }
            }
        }
        }],
        
      }

}
const brainReducer = (state = initalState, action) => {
    const { type, payload } = action;

    switch (type) {
        case "AWAITING_BITCOIN":
          return {
            ...state,
            loading: true
          }
        case "REJECTED_BITCOIN":
          return {
            ...state,
            loading: false,
          }
        case "SUCCESS_BITCOIN":

            return {
                ...state,
                loading: false,
                dataD: {
                      labels: payload.laBox,
                      datasets: [{
                        type: "line",
                        label: ["REAL-Time Price" ],
                        data: payload.opVS,
                        backgroundColor: 'rgb(0, 0, 0, 0.6)',
                        borderColor: 'rgb(0, 0, 0)',
                        pointBorderColor: 'rgb(0, 0, 0)',
                        order: 1,
                        options: {
                            scales: {
                                x: 50
                            }
                        }
                      },
                      {
                        type: 'line',
                        label: "OPEN PREDICTION",
                        data: payload.brOP,
                        backgroundColor: 'rgba(255, 0, 0, 0.4)',
                        borderColor: '	rgba(255, 0, 0, 0.9)',
                        pointBorderColor: 'rgba(25, 16, 0, 1)',
                        order: 2,
                        options: {
                            scales: {
                                x: 50
                            }
                        }
                      },
                      {
                        type: 'line',
                        label: ["fib(0.34)", "fib(0.21)", "fib(0.13)", "fib(0.08)"],
                        data: payload.midOP,// [payload.midOP * 0.21, payload.midOP * 0.13, payload.midOP * 0.08],
                        backgroundColor: 'rgba(255,255,0, 0.7)',
                        borderColor: '	rgba(255, 255, 0, 1)',
                        pointBorderColor: 'rgba(255, 255, 0, 1)',
                        order: 3,
                        options: {
                            scales: {
                                x: 50
                            }
                        }
                      },
                    ]
                  },
                }
                 default: return state;
                }
             }
             export default brainReducer;
